#home {
  display: flex;
  width: 100%;
  background-size: cover;
  background-image: url("../../images/backgrounds/background-1.jpg");
  min-height: calc(100vh - 90px);
  height: fit-content;
}
#home .home-content {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  position: relative;
}
#home .home-content .welcome {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 20%;
  color: white;
  font-weight: bold;
  font-size: 36px;
}
#home .home-content .welcome > span {
  margin: 0 10px;
  color: #ffde03;
  font-size: 38px;
}
