/* Start Basic Component */
::selection {
  background-color: #d50000;
  color: #ffde03;
}
::-moz-selection {
  background-color: #d50000;
  color: #ffde03;
}

.navlink {
  color: black;
  text-decoration: none;
  font-size: 18px;
  transition: all 0.3s ease;
}
.navlink:hover {
  color: #d50000;
  border-radius: 4px;
}

.navlink.active {
  font-weight: bold;
  color: #d50000;
  text-decoration: none;
}
/* End Basic Component */

body {
  overflow-x: hidden; /* Hide horizontal scrollbar */
}

/* Start The Main Wrapper */
.wrapper {
  display: flex;
  flex-direction: column;
  padding-top: 50px;
  width: 100%;
}
/* End The Main Wrapper */

/* Start Header */
.head {
  display: flex;
  align-items: center;
  background-color: #222d32;
  padding: 15px;
  border-bottom: 2px solid #d50000;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 50px;
  z-index: 5;
}
.head a {
  font-size: 28px;
  color: #ffde03 !important;
}
/* End Header */

/* Start Main Content */
#content {
  display: flex;
  width: 100%;
  height: calc(100vh - 50px);
  overflow: hidden;
}

/* Start Sidebar */
#sidebar {
  display: flex;
  flex-direction: column;
  background-color: #222d32;
  color: white;
  width: 18% !important;
  height: 100%;
}

#sidebar .ScrollbarsCustom-TrackY {
  background-color: #222d32 !important;
  top: 5px !important;
  border-radius: 2px !important;
  height: calc(100% - 10px) !important;
}
#sidebar .ScrollbarsCustom-ThumbY {
  background-color: #1c4355 !important;
  border-radius: 2px !important;
  transition: 0.2s ease !important;
}
#sidebar .ScrollbarsCustom-ThumbY:hover {
  background-color: #456a7a !important;
}
#sidebar .side-main-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 5px;
  width: 100%;
  padding: 10px 5px 0;
}
#sidebar .side-main-header .sidebar-header {
  width: 85%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgb(243, 73, 30);
}
#sidebar .side-main-header .sidebar-header h3 {
  font-size: 24px;
  /* display: none; */
}
#sidebar .side-main-header .sidebar-header strong {
  font-size: 24px;
  display: none;
}
#sidebar .side-main-header #dismiss {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 35px;
  width: 15%;
  font-size: 22px;
  background-color: #222d32;
  border-radius: 4px;
  transition: all 0.4s ease;
}
#sidebar .side-main-header #dismiss:hover {
  background-color: white;
  color: #222d32;
  border-radius: 4px;
}
#sidebar ul.components {
  display: flex;
  flex-direction: column;
}
#sidebar ul.components p {
  background-color: rgba(255, 255, 255, 0.1);

  margin: 0;
  padding: 5px;
}
#sidebar ul.components > li {
  display: flex;
  flex-direction: column;
  width: 100%;
}
#sidebar ul.components > li > a:hover {
  background-color: white;
}
#sidebar ul.components > li > a {
  padding: 5px;
  display: flex;
  align-items: center;
  color: chocolate;
  width: 100%;
  text-decoration: none;
  font-size: 18px;
  transition: all 0.3s ease;
}
#sidebar ul.components > li.has-menu.active > a {
  font-weight: bold;
  background-color: white;
  color: rgb(243, 73, 30);
  border-bottom: 2px solid rgb(243, 73, 30);
}
#sidebar ul.components > li.has-menu a {
  justify-content: space-between;
}
#sidebar ul.components > li a span {
  display: flex;
  align-items: center;
  gap: 5px;
}
#sidebar ul.components > li a i {
  text-align: center;
  margin: 0 5px;
  transition: all 0.3s ease-in-out;
}
#sidebar ul.components > li.active a i.arrow {
  transform: rotate(90deg);
}
#sidebar ul.components ul {
  /* display: none; */
  display: flex;
  flex-direction: column;
  margin-left: 20px;
  gap: 2px;
}
#sidebar ul.components ul li {
  transition: 0.4s ease;
  padding: 5px 0;
}
#sidebar ul.components ul li:hover {
  /* border-radius: 2px 0 0 2px; */
  background-color: white;
}
#sidebar ul.components ul li:hover a {
  color: rgb(16, 128, 128);
  font-weight: 700;
}
#sidebar ul.components ul a {
  padding: 0 10px;
  text-decoration: none;
  color: rgb(26, 211, 211);
  font-weight: 500;
}
#sidebar ul.components ul.active {
  display: flex;
}
/* End Sidebar */

/* Start Main Section */
#main-section {
  display: flex;
  flex-direction: column;
  width: 100% !important;
}
#main-section .ScrollbarsCustom-TrackY {
  background-color: white !important;
  top: 5px !important;
  height: calc(100% - 5px) !important;
  border-radius: 2px !important;
}
#main-section .ScrollbarsCustom-ThumbY {
  border-radius: 2px !important;
  background-color: #d5d8db !important;
  transition: 0.2s ease !important;
}
#main-section .ScrollbarsCustom-ThumbY:hover {
  background-color: #b0b4b8 !important;
}
/* Start Main Section */

/* Start Navbar */
#navbar {
  height: 40px;
  display: flex;
  width: 100%;
  padding: 0;
  background-color: rgba(235, 235, 235, 0.712) !important;
  border-bottom: 1px solid rgba(214, 213, 213, 0.616);
}
#navbar .container-fluid {
  display: flex;
  width: 100%;
  justify-content: space-between;
  height: 100%;
  min-height: 40px;
}
#navbar .container-fluid .links-container {
  width: fit-content;
  display: flex;
  align-items: center;
  height: 100%;
  gap: 5px;
}
#navbar .container-fluid .links-container a.navlink {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 60px;
}
#navbar .container-fluid .links-container a.navlink:hover {
  background-color: #dacbcb7a;
}
#navbar .container-fluid .links-container a.navlink.active {
  border-width: 1px;
  border-style: solid;
  border-color: #b0b4b82c #b0b4b856 transparent #b0b4b856;
  border-radius: 4px 4px 0 0;
  border-bottom: 2px solid #d50000 !important;
  background-color: #dacbcb7a;
}
#navbar .links-container a.navlink.active > span {
}
#navbar .container-fluid .links-container a.navlink > span {
  padding: 0 5px;
}

/* Start Cart Icon */
#navbar .container-fluid .cart-icon {
  margin: 0 10px;
}
#navbar .container-fluid .cart-icon:hover i {
  color: #4a6b7e;
}
#navbar .container-fluid .cart-icon i {
  font-size: 24px;
  color: #222d32;
  line-height: 40px;
  position: relative;
  transition: all 0.3s ease;
}
#navbar .container-fluid .cart-icon span {
  position: absolute;
  top: 1px;
  right: -5px;
  font-size: 12px;
  font-family: Arial, Helvetica, sans-serif;
  padding: 2.5px 4px;
}
/* Start Cart Icon */

/* End Navbar */

/* Start Pages */
#pages {
  display: flex;
  flex-direction: column;
  min-height: calc(100vh - 130px);
  width: 100%;
  height: fit-content;
  position: relative;
}

#pages .card {
  display: flex;
  flex-direction: column;
  height: 450px;
  margin-bottom: 20px;
  border: none;
  background-color: #ffa704;
  padding: 5px;
  overflow: hidden;
}
#pages .card img {
  text-align: center;
  min-height: 240px;
  height: 240px;
  object-fit: contain;
  border-radius: 8px;
  background-color: white;
}
#pages .card .card-body {
  display: flex;
  flex-direction: column;
  padding: 0;
}

#pages .card .card-body h4 {
  text-align: center;
  padding: 10px 0 5px;
  font-weight: bold;
  color: #d50000;
  min-height: 35px;
  height: 35px;
  margin: 0;
  font-size: 22px;
  overflow: hidden;
  letter-spacing: 2px;
}

#cart .card-body h4 {
  color: #ff2e2e !important;
  min-height: 45px !important;
  height: 45px !important;
  margin-bottom: 10px !important;
  border-bottom: 1px solid rgba(255, 255, 255, 0.788);
}
#pages .card .card-body p.quantity {
  color: black;
  min-height: 24px;
  height: 26px;
  margin: 0;
  font-size: 15px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 3px;
  border-bottom: 1px solid #ffc353;
}
#pages .card .card-body p.quantity > span {
  color: green;
  font-weight: bold;
}
#pages .card .card-body h5,
#cart h5 {
  padding: 0 5px;
  min-height: 35px;
  height: 35px;
  margin: 0;
  font-size: 20px;
  overflow: hidden;
  line-height: 40px;
  display: flex;
  align-items: center;
  margin-top: 2px;
}
#cart h5 {
  min-height: 40px;
  height: 40px;
}
#pages .card .card-body h5 .price,
#cart h5 .price {
  display: flex;
  align-items: center;
  background-color: white;
  border-radius: 6px;
  color: #d50000;
  margin: 0 4px;
  padding: 0 6px;
  height: 80%;
  font-weight: bold;
  font-size: 22px;
}
#pages .card .card-body h5 .price span,
#cart h5 .price span {
  padding: 2px;
  color: green;
  font-weight: 500;
}

#pages .card .card-body p {
  width: 100%;
  min-height: 65px;
  height: 65px;
  margin: 0 5px 5px;
  display: flex;
  flex-direction: column;
}
#product .card-body .ScrollbarsCustom,
#products .card-body .ScrollbarsCustom {
  height: 65px !important;
}

#product .card-body .ScrollbarsCustom-Content,
#products .card-body .ScrollbarsCustom-Content {
  overflow-x: clip !important;
}

#product .card-body .ScrollbarsCustom-TrackY,
#products .card-body .ScrollbarsCustom-TrackY {
  background-color: #ffa704 !important;
  top: 5px !important;
  border-radius: 2px !important;
  height: calc(100% - 10px) !important;
}
#product .card-body .ScrollbarsCustom-ThumbY,
#products .card-body .ScrollbarsCustom-ThumbY {
  background-color: #ad750c !important;
  border-radius: 2px !important;
  transition: 0.2s ease !important;
}
#product .card-body .ScrollbarsCustom-ThumbY:hover,
#products .card-body .ScrollbarsCustom-ThumbY:hover {
  background-color: #d1911b !important;
}
#pages .card .card-body .cart-details {
  padding: 5px;
  width: 100%;
  height: 70px;
}
#pages .card .card-body .cart-details h6 {
  display: flex;
  gap: 5px;
}
#pages .card .card-body .cart-details h6 .price {
  color: #ff2e2e;
  font-size: 18px;
}
#pages .card .card-body .cart-details h6 .price span {
  padding: 2px;
  color: green;
  font-weight: 500;
}

#pages .card .card-body p::-webkit-scrollbar {
  display: none;
}
#pages .card .card-body a {
  background-color: #028507;
  border: none;
  font-size: 18px;
  transition: 0.3s ease;
  margin: auto;
  width: 95%;
  overflow: hidden;
  height: 33px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
  margin-top: 4px;
}
#pages .card .card-body a:hover {
  background-color: #109214;
}
#product .card .card-body a {
  height: 40px !important;
  margin: 0 !important;
}
#pages .card .card-body a.delete-card {
  background-color: #d50000;
  width: 60%;
  margin: 0 auto !important;
}
#pages .card .card-body a.delete-card i {
  font-size: 16px;
}
@media (max-width: 1024px) {
  #pages .card .card-body a.delete-card {
    width: 95%;
  }
}
#pages .card .card-body a.delete-card:hover {
  background-color: #b71c1c;
}
#pages .card .card-body a i {
  padding: 0 5px;
  font-size: 20px;
}

/* End Pages */

/* End Main Content */

/* Start Footer */
.foot {
  display: flex;
  background-color: #222d32;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  word-spacing: 1.5px;
  letter-spacing: 1.5px;
  width: 100%;
  height: 40px;
  color: white;
  position: absolute;
  bottom: -40px;
  left: 0;
}

.foot span {
  color: #ffde03;
  font-weight: bold;
  font-size: 22px;
  margin: 0 5px;
}
/* End Footer */

@media (max-width: 992px) {
  #sidebar {
    width: 25% !important;
  }
  #main-section {
    width: 75% !important;
  }
}
@media (max-width: 768px) {
  #sidebar {
    width: 40% !important;
  }
  #main-section {
    width: 60% !important;
  }
  #pages #products .col-3 {
    width: 100%;
  }
}

/* Start Product */
#product {
  padding: 0;
  margin: 0;
  margin: auto;
}
#product .card {
  flex-direction: column;
  height: 400px;
  gap: 10px;
}
#product .card > h4 {
  text-align: center;
  height: 40px;
  line-height: 40px;
  margin: 0;
  font-size: 26px;
  border-bottom: 1px solid rgba(121, 121, 121, 0.397);
}
#product .card .card-section {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 5%;
  height: calc(98% - 40px);
}
#product .card img {
  width: calc(95% / 2);
}
#product .card .card-body {
  width: calc(95% / 2);
  height: 100%;
  justify-content: space-evenly;
  padding: 10px;
}
#product .card .card-body h4 {
  font-size: 32px;
  height: fit-content;
}
#product .card .card-body .available {
  display: flex;
  align-items: center;
}
#product .card .card-body .available > span {
  color: green;
  margin: 0 4px;
  font-weight: bold;
}
#product .card .card-body .amount-container {
  display: flex;
  flex-direction: column;
}
#product .card .card-body .amount-container > p {
  height: fit-content;
  font-size: 18px;
}
#product .card .card-body .amount {
  display: flex;
  height: 40px;
  width: 100%;
  justify-content: space-between;
}
#product .card .card-body .amount input {
  width: 80px;
  border-radius: 4px 0 0 4px;
}
#product .card .card-body .amount a {
  width: calc(100% - 80px);
  padding: 0;
  line-height: 40px;
  border-radius: 0 4px 4px 0;
}

/* End Product */

/* Start Cart */
#cart {
  display: flex;
  flex-direction: column;
}
#cart .card {
  background-color: #042722e7;
  color: white;
}
#cart .pay {
  width: 90%;
  margin: 6px auto;
  font-size: 22px;
  font-weight: 500;
  letter-spacing: 2px;
  background-color: #159119;
  border: none;
  transition: 0.3s ease;
}
#cart .pay:hover {
  background-color: #24a528;
}
/* End Cart */
